import React, { useState, useEffect } from 'react';
import { useLanguage } from '../../context/LanguageContext';
import { useAuth } from '../../context/AuthContext';
import { XMarkIcon } from '@heroicons/react/24/outline';
import { authenticatedFetch } from '../../utils/auth';
import { auditTechnicianAction, AUDIT_ACTIONS } from '../../utils/audit';

/**
 * Format phone number as user types: (XXX) XXX-XXXX
 * @param {string} value - The input value
 * @returns {string} - Formatted phone number
 */
const formatPhoneNumber = (value) => {
  // Remove all non-digit characters
  const cleaned = value.replace(/\D/g, '');
  
  // Limit to 10 digits
  const limited = cleaned.substring(0, 10);
  
  // Apply formatting based on length
  if (limited.length <= 3) {
    return limited;
  } else if (limited.length <= 6) {
    return `(${limited.substring(0, 3)}) ${limited.substring(3)}`;
  } else {
    return `(${limited.substring(0, 3)}) ${limited.substring(3, 6)}-${limited.substring(6)}`;
  }
};

/**
 * InviteTechnician Modal Component
 * Provides three ways to invite technicians: Create Your Own, Send Email, Send SMS
 */
const InviteTechnician = ({ isOpen, onClose, onSuccess }) => {
  const { t } = useLanguage();
  const { user } = useAuth();
  
  const [activeTab, setActiveTab] = useState('email'); // Default to Send Email
  const [isLoading, setIsLoading] = useState(false);
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(null);

  // Form states for each tab
  const [createForm, setCreateForm] = useState({
    fullName: '',
    email: '',
    phone: '',
    password: '',
    confirmPassword: ''
  });

  const [emailForm, setEmailForm] = useState({
    techName: '',
    email: ''
  });

  const [smsForm, setSmsForm] = useState({
    techName: '',
    phone: ''
  });

  // Clear messages when tab changes
  useEffect(() => {
    setError(null);
    setSuccess(null);
  }, [activeTab]);

  // Reset forms and states when modal opens/closes
  useEffect(() => {
    if (!isOpen) {
      setActiveTab('email');
      setIsLoading(false);
      setError(null);
      setSuccess(null);
      setCreateForm({ fullName: '', email: '', phone: '', password: '', confirmPassword: '' });
      setEmailForm({ techName: '', email: '' });
      setSmsForm({ techName: '', phone: '' });
    }
  }, [isOpen]);

  // API Base URL
  const API_BASE_URL = process.env.REACT_APP_API_URL || 
    (window.location.hostname === 'localhost' ? 
      'http://localhost:8000/api/v1' : 
      'https://jobticketinvoice-backend.onrender.com/api/v1');

  // Early return after all hooks
  if (!isOpen) return null;

  // Handle phone number changes for Create Your Own form
  const handleCreatePhoneChange = (e) => {
    const rawValue = e.target.value.replace(/\D/g, ''); // Store only digits
    setCreateForm({...createForm, phone: rawValue});
  };

  // Handle phone number changes for SMS form
  const handleSmsPhoneChange = (e) => {
    const rawValue = e.target.value.replace(/\D/g, ''); // Store only digits
    setSmsForm({...smsForm, phone: rawValue});
  };

  // Handle form submissions
  const handleCreateSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    setError(null);

    // Validation
    if (createForm.password !== createForm.confirmPassword) {
      setError(t('manager.techManagement.inviteForm.passwordMismatch'));
      setIsLoading(false);
      return;
    }

    if (createForm.password.length < 8) {
      setError(t('manager.techManagement.inviteForm.passwordMinLengthError'));
      setIsLoading(false);
      return;
    }

    try {
      // Use the existing working endpoint that's already deployed
      const response = await authenticatedFetch('/invitations/create-technician-direct', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          email: createForm.email,
          name: createForm.fullName,
          job_type: 'other', // Using correct enum value
          temporary_password: createForm.password
        })
      });

      if (!response.ok) {
        // Try to get error details from response
        let errorData;
        try {
          errorData = await response.json();
        } catch (parseErr) {
          // If JSON parsing fails, try to get text
          try {
            errorData = { detail: await response.text() };
          } catch (textErr) {
            errorData = { detail: `HTTP ${response.status}: ${response.statusText}` };
          }
        }
        
        // Create a detailed error object
        const error = new Error(errorData.detail || t('manager.techManagement.inviteForm.createError'));
        error.response = response;
        error.responseData = errorData;
        throw error;
      }

      const result = await response.json();
      setSuccess(t('manager.techManagement.inviteForm.createSuccess', { name: createForm.fullName }));
      
      // Call onSuccess callback with the new technician data
      if (onSuccess) {
        onSuccess(result);
      }
      
      // Close modal after 2 seconds
      setTimeout(() => {
        onClose();
      }, 2000);

    } catch (err) {
      console.error('Tech account creation error:', err);
      
      // Comprehensive error gathering
      let errorMessage = t('manager.techManagement.inviteForm.createError');
      let errorDetails = [];
      
      try {
        if (err.response) {
          // Server responded with error status
          errorDetails.push(`Status: ${err.response.status}`);
          errorDetails.push(`Status Text: ${err.response.statusText}`);
          errorDetails.push(`URL: ${err.response.url || '/invitations/create-technician-direct'}`);
          
          if (err.responseData) {
            if (typeof err.responseData === 'string') {
              errorDetails.push(`Response: ${err.responseData}`);
            } else if (err.responseData.detail) {
              errorMessage = err.responseData.detail;
              errorDetails.push(`Detail: ${err.responseData.detail}`);
            } else {
              errorDetails.push(`Response: ${JSON.stringify(err.responseData)}`);
            }
          }
        } else if (err.message) {
          // Network or other error
          errorMessage = err.message;
          errorDetails.push(`Error: ${err.message}`);
        }
        
      } catch (processingErr) {
        errorDetails.push(`Error processing error: ${processingErr.message}`);
      }
      
      // Log comprehensive error details to console
      console.error('Comprehensive error details:', {
        originalError: err,
        errorMessage,
        errorDetails,
        timestamp: new Date().toISOString(),
        url: '/invitations/create-technician-direct',
        method: 'POST',
        requestBody: {
          email: createForm.email,
          name: createForm.fullName,
          job_type: 'other',
          temporary_password: createForm.password
        }
      });
      
      // Show detailed error to user in development, generic in production
      const isDevelopment = process.env.NODE_ENV === 'development';
      if (isDevelopment && errorDetails.length > 0) {
        setError(`${errorMessage}\n\nDEBUG INFO:\n${errorDetails.join('\n')}`);
      } else {
        setError(errorMessage);
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleEmailSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    setError(null);

    try {
      const response = await authenticatedFetch('/tech-invites/send-email', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          tech_name: emailForm.techName,
          email: emailForm.email
        })
      });

      if (response.ok) {
        const data = await response.json();
        
        // Log audit event for technician invitation
        await auditTechnicianAction(
          AUDIT_ACTIONS.TECHNICIAN_INVITED,
          emailForm.email,
          { 
            tech_name: emailForm.techName,
            invitation_method: 'email'
          }
        );
        
        setSuccess(t('manager.techManagement.inviteForm.emailSent'));
        setEmailForm({ techName: '', email: '' });
        
        // Close modal after 2 seconds
        setTimeout(() => {
          onClose();
        }, 2000);
      } else {
        const errorData = await response.json();
        throw new Error(errorData.detail || 'Failed to send email invitation');
      }
    } catch (err) {
      console.error('Email invitation error:', err);
      setError(err.message);
    } finally {
      setIsLoading(false);
    }
  };

  const handleSmsSubmit = async (e) => {
    e.preventDefault();
    setIsLoading(true);
    setError(null);

    try {
      const response = await authenticatedFetch('/tech-invites/send-sms', {
        method: 'POST',
        headers: {
          'Content-Type': 'application/json'
        },
        body: JSON.stringify({
          tech_name: smsForm.techName,
          phone: smsForm.phone
        })
      });

      if (response.ok) {
        const data = await response.json();
        
        // Log audit event for technician invitation
        await auditTechnicianAction(
          AUDIT_ACTIONS.TECHNICIAN_INVITED,
          smsForm.phone,
          { 
            tech_name: smsForm.techName,
            invitation_method: 'sms'
          }
        );
        
        setSuccess(t('manager.techManagement.inviteForm.smsSent'));
        setSmsForm({ techName: '', phone: '' });
        
        // Close modal after 2 seconds
        setTimeout(() => {
          onClose();
        }, 2000);
      } else {
        const errorData = await response.json();
        throw new Error(errorData.detail || 'Failed to send SMS invitation');
      }
    } catch (err) {
      console.error('SMS invitation error:', err);
      setError(err.message);
    } finally {
      setIsLoading(false);
    }
  };

  return (
    <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50 p-4">
      <div className="bg-white rounded-lg shadow-xl max-w-2xl w-full max-h-[90vh] overflow-y-auto">
        {/* Header */}
        <div className="flex items-center justify-between p-6 border-b border-gray-200">
          <h2 className="text-xl font-semibold text-gray-900 mb-4">
            {t('manager.techManagement.inviteForm.title')}
          </h2>
          <button
            onClick={onClose}
            className="text-gray-400 hover:text-gray-600 transition-colors"
          >
            <XMarkIcon className="h-6 w-6" />
          </button>
        </div>

        {/* Tab Navigation */}
        <div className="border-b border-gray-200">
          <nav className="flex space-x-8 px-6">
            <button
              onClick={() => setActiveTab('create')}
              className={`py-4 px-1 border-b-2 font-medium text-sm transition-colors ${
                activeTab === 'create'
                  ? 'border-blue-500 text-blue-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              {t('manager.techManagement.inviteForm.createTab')}
            </button>
            <button
              onClick={() => setActiveTab('email')}
              className={`py-4 px-1 border-b-2 font-medium text-sm transition-colors ${
                activeTab === 'email'
                  ? 'border-blue-500 text-blue-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              {t('manager.techManagement.inviteForm.emailTab')}
            </button>
            <button
              onClick={() => setActiveTab('sms')}
              className={`py-4 px-1 border-b-2 font-medium text-sm transition-colors ${
                activeTab === 'sms'
                  ? 'border-blue-500 text-blue-600'
                  : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
              }`}
            >
              {t('manager.techManagement.inviteForm.smsTab')}
            </button>
          </nav>
        </div>

        {/* Content */}
        <div className="p-6">
          {/* Success Message */}
          {success && (
            <div className="mb-4 p-4 bg-green-50 border border-green-200 rounded-md">
              <p className="text-green-800">{success}</p>
            </div>
          )}

          {/* Error Message */}
          {error && (
            <div className="mb-4 p-4 bg-red-50 border border-red-200 rounded-md">
              <p className="text-red-800">{error}</p>
            </div>
          )}

          {/* Create Your Own Tab */}
          {activeTab === 'create' && (
            <form onSubmit={handleCreateSubmit} className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  {t('manager.techManagement.inviteForm.fullNameRequired')}
                </label>
                <input
                  type="text"
                  required
                  value={createForm.fullName}
                  onChange={(e) => setCreateForm({...createForm, fullName: e.target.value})}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder={t('manager.techManagement.inviteForm.enterFullName')}
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  {t('manager.techManagement.inviteForm.emailAddressRequired')}
                </label>
                <input
                  type="email"
                  required
                  value={createForm.email}
                  onChange={(e) => setCreateForm({...createForm, email: e.target.value})}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder={t('manager.techManagement.inviteForm.enterEmail')}
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  {t('manager.techManagement.inviteForm.phoneNumber')}
                </label>
                <input
                  type="tel"
                  value={formatPhoneNumber(createForm.phone)}
                  onChange={handleCreatePhoneChange}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder={t('manager.techManagement.inviteForm.enterPhone')}
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  {t('manager.techManagement.inviteForm.passwordRequired')} <span className="text-gray-400 font-normal">{t('manager.techManagement.inviteForm.passwordMinLength')}</span>
                </label>
                <input
                  type="password"
                  required
                  value={createForm.password}
                  onChange={(e) => setCreateForm({...createForm, password: e.target.value})}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder={t('manager.techManagement.inviteForm.enterPassword')}
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  {t('manager.techManagement.inviteForm.confirmPasswordRequired')}
                </label>
                <input
                  type="password"
                  required
                  value={createForm.confirmPassword}
                  onChange={(e) => setCreateForm({...createForm, confirmPassword: e.target.value})}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder={t('manager.techManagement.inviteForm.confirmPasswordPlaceholder')}
                />
              </div>
              
              <button
                type="submit"
                disabled={isLoading}
                className="w-full bg-blue-600 text-white py-2 px-4 rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
              >
                {isLoading ? t('manager.techManagement.inviteForm.creating') : t('manager.techManagement.inviteForm.createTechnician')}
              </button>
            </form>
          )}

          {/* Send Email Tab */}
          {activeTab === 'email' && (
            <form onSubmit={handleEmailSubmit} className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  {t('manager.techManagement.inviteForm.technicianNameRequired')}
                </label>
                <input
                  type="text"
                  required
                  value={emailForm.techName}
                  onChange={(e) => setEmailForm({...emailForm, techName: e.target.value})}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder={t('manager.techManagement.inviteForm.enterTechnicianName')}
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  {t('manager.techManagement.inviteForm.emailAddressRequired')}
                </label>
                <input
                  type="email"
                  required
                  value={emailForm.email}
                  onChange={(e) => setEmailForm({...emailForm, email: e.target.value})}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder={t('manager.techManagement.inviteForm.enterEmail')}
                />
              </div>
              
              <div className="bg-blue-50 border border-blue-200 rounded-md p-4">
                <p className="text-sm text-blue-800">
                  {t('manager.techManagement.inviteForm.emailInviteInfo')}
                </p>
              </div>
              
              <button
                type="submit"
                disabled={isLoading}
                className="w-full bg-blue-600 text-white py-2 px-4 rounded-md hover:bg-blue-700 focus:outline-none focus:ring-2 focus:ring-blue-500 disabled:opacity-50 disabled:cursor-not-allowed transition-colors"
              >
                {isLoading ? t('manager.techManagement.inviteForm.sending') : t('manager.techManagement.inviteForm.sendEmailInvitation')}
              </button>
            </form>
          )}

          {/* Send SMS Tab */}
          {activeTab === 'sms' && (
            <form onSubmit={handleSmsSubmit} className="space-y-4">
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  {t('manager.techManagement.inviteForm.technicianNameRequired')}
                </label>
                <input
                  type="text"
                  required
                  value={smsForm.techName}
                  onChange={(e) => setSmsForm({...smsForm, techName: e.target.value})}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder={t('manager.techManagement.inviteForm.enterTechnicianName')}
                />
              </div>
              
              <div>
                <label className="block text-sm font-medium text-gray-700 mb-1">
                  {t('manager.techManagement.inviteForm.phoneNumberRequired')}
                </label>
                <input
                  type="tel"
                  required
                  value={formatPhoneNumber(smsForm.phone)}
                  onChange={handleSmsPhoneChange}
                  className="w-full px-3 py-2 border border-gray-300 rounded-md focus:outline-none focus:ring-2 focus:ring-blue-500"
                  placeholder={t('manager.techManagement.inviteForm.enterPhone')}
                />
              </div>
              
              <div className="bg-yellow-50 border border-yellow-200 rounded-md p-4">
                <p className="text-sm text-yellow-800">
                  {t('manager.techManagement.inviteForm.smsInviteInfo')}
                </p>
              </div>
              
              <button
                type="submit"
                disabled={true}
                className="w-full bg-gray-400 text-white py-2 px-4 rounded-md cursor-not-allowed"
              >
                {t('manager.techManagement.inviteForm.smsInviteComingSoon')}
              </button>
            </form>
          )}
        </div>

        {/* Footer */}
        <div className="flex justify-end space-x-3 p-6 border-t border-gray-200">
          <button
            onClick={onClose}
            className="px-4 py-2 text-gray-700 bg-gray-100 rounded-md hover:bg-gray-200 focus:outline-none focus:ring-2 focus:ring-gray-500"
          >
            {t('manager.techManagement.inviteForm.cancel')}
          </button>
        </div>
      </div>
    </div>
  );
};

export default InviteTechnician;
